@import "../../../index.scss";

.education-container {
    margin-top: 4rem;
    @include flexCenter(stretch, center, column, 0);
}

.education-item-container {
    @include flexCenter(center, center, column, 1rem);
    padding: 0 2rem;
    align-items: stretch;
    padding-bottom: 1rem;
}

.education-heading {
    font-size: 1.8rem;
    color: $form-header-color;
    font-weight: 600;
    padding-left: 2.4rem;
    margin-bottom: 1rem;
}

.education-input {
    padding: 0.5rem;
    padding: 0.5rem;
    padding-left: 1rem;
    font-size: 1.5rem;
    font-weight: 400;
    border: none;
    border-radius: 0.6rem;
}

.education-input:focus {
    outline: none;
}

.education-btn-container {
    @include flexCenter(stretch, center, column, 0.5rem);
}
